import React, { useEffect, useState }  from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import { RiHeart3Line, RiStore3Line, RiHeart3Fill } from "react-icons/ri";
import { FiTruck  } from "react-icons/fi";
import { IoIosArrowForward  } from "react-icons/io";

const FuritemdetailBlock = styled.div`
    margin-top:100px;
    .inner {
        font-size:14px; font-weight:300; 
        .furmenu { 
            padding-bottom:15px; border-bottom:1px solid #ddd;
            a { padding-right:10px; &:hover { text-decoration:underline; } }
            span { padding-left:10px; }
        }
        .furcont {
            display:flex; padding:60px 0; width:100%;
            .furR {
                margin-right:5%; width:68%;
                .furimg {
                    display:flex;
                    img { padding: 10px; }
                }   
                p { padding-top: 80px; width:50%; }           
            }
            .furL {
                width:27%; line-height:2;
                .furtxt {
                    border-bottom:1px solid #ddd;
                    padding-bottom: 30px;
                    h2 { 
                        font-size:22px; display:flex; justify-content:space-between; 
                    }
                }
                .furbtn {
                    padding: 20px 0 30px; border-bottom:1px solid #ddd;
                    p:nth-child(1) {
                        display:flex; justify-content:space-between; align-items:center;
                        button { 
                            cursor:pointer;
                            background:#0058a3; width:80%; color:#fff; font-weight:800; 
                            padding:15px 0; border-radius:50px;
                            &:hover { background:#00447f; }
                        }
                        span { 
                            cursor:pointer;
                            background:#fff; border:1px solid #ddd; 
                            padding:2px 15px; font-size:22px; border-radius:50px;
                        }
                    }
                    p:nth-child(2) {
                        padding: 35px 0 10px;
                    }
                }
                
            }
        }
    }

`;

const Furitemdetail = (props) => {
    console.log(props)
    const {location, history} = props

    useEffect(
        () => {
            if (location.state === undefined) {
                history.push('/')
            } 
        }
        ,[]
    )

    const [heart, setHeart] = useState( <RiHeart3Line /> );
    const changeheart = () => {
        setHeart(<RiHeart3Fill />)
    };
    
    if (location.state) {
        return (
            <div>
                <Header />
                <FuritemdetailBlock>
                    <div className="inner">
                        <div className="furmenu">
                            <p>
                                <Link to="/">홈</Link><IoIosArrowForward /><span>제품</span>
                            </p>
                        </div>
                        <div className="furcont">
                            <div className="furR">
                                <div className="furimg">
                                    <img src={location.state.photo} alt={location.state.name} />
                                    <img src={location.state.photo2} alt={location.state.name} />
                                </div>
                                <p>{location.state.explain}</p>
                            </div>
                            <div className="furL">
                                <div className="furtxt">
                                    <h2><span>{location.state.name}</span><span>{location.state.price}</span></h2>
                                    <p>{location.state.description}</p>
                                    <p>{location.state.score}</p>
                                </div> 
                                <div className="furbtn">
                                    <p><button>구매하기</button><span onClick={changeheart}>{heart}</span></p>
                                    <p><FiTruck /><span>배송 여부는 결제 단계에서 확인하실 수 있습니다.</span></p>
                                    <p><RiStore3Line /><span>매장 재고 및 재입고 날짜 확인</span></p>
                                </div> 
                            </div>
                        </div>
                    </div>
                </FuritemdetailBlock>
                <Footer />
            </div>
        );
    } else {
        return null
    }
};

export default Furitemdetail;