import React, {useState,useCallback} from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { IoIosArrowForward  } from "react-icons/io";
import Homeidea from './Homeidea';

const IdeasectionBlock = styled.div`
    padding:100px 0;
    .inner {
        font-weight:300; 
        .ideamenu { 
            font-size:14px;
            padding-bottom:15px; border-bottom:1px solid #ddd;
            a { padding-right:10px; &:hover { text-decoration:underline; } }
            span { padding-left:10px; }
        }
        h2 { padding: 60px 0 40px; font-size: 36px; font-weight: 600; }
    }

`;

const Ideasection = () => {
    const [activeTab, setActiveTab] = useState(0)
    const onSelect = useCallback(
            (id) => {
                setActiveTab(id)
            },[]
        )
    
    return (
        <IdeasectionBlock>
            <div className="inner">
                <div className="ideamenu">
                    <p>
                        <Link to="/">홈</Link><IoIosArrowForward /><span>제품</span>
                    </p>
                </div>
                <h2>홈퍼니싱 아이디어 더 보기</h2> 
                <Homeidea onSelect={onSelect} activeTab={activeTab} />
            </div>
        </IdeasectionBlock>
    );
};

export default Ideasection;