import React from 'react';
import styled from 'styled-components';

const ProductitembottomBlock = styled.div`
    float:left; width:50%; padding: 7px;
    position:relative;
    .protext {
        position: absolute; top:40%; left:50%;
        background:#fff; padding:10px;
`;

const Productitembottom = ({ picture, name, explain, price}) => {

    return (
        <ProductitembottomBlock>
            <div className="proimgbox2">
                <img src={picture} alt={name} />
                <div className="protext">
                    <h4>{name}</h4>
                    <p>{explain}</p>
                    <p>{price}</p>
                </div>
            </div>            
        </ProductitembottomBlock>
    );
};

export default Productitembottom;