import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { IoIosArrowForward  } from "react-icons/io";

const NewitemssectionBlock = styled.div`
    padding:100px 0;
    .inner {
        font-weight:300; 
        .showmenu { 
            font-size:14px;
            padding-bottom:15px; border-bottom:1px solid #ddd;
            a { padding-right:10px; &:hover { text-decoration:underline; } }
            span { padding-left:10px; }
        }
        h2 { font-size:36px; padding: 60px 0 30px; }
        h3 { font-size:25px; }
        .pbox {
            width:100%; display:flex; justify-content:space-between; 
            padding:10px 0 30px; font-size:14px;
            span { width:50%; }
            a { 
                padding: 10px 20px; border:1px solid #ddd; border-radius:50px;
                font-weight:600;
            }
        }
        .arti4imgbox {
            display: flex; 
            
            .dot { 
                background: rgba(0,0,0,0.4); 
                width: 35px; height: 35px; border-radius: 50%;
                position: relative; transition: all 0.5s;
                border: 2px solid rgba(255,255,255,0);
                span { 
                    background: #fff; 
                    width: 14px; height: 14px; border-radius: 50%;
                    box-shadow: 0 0 5px #333;
                    position: absolute; top: 50%; left: 50%; transform: translate(-50%,-50%);
                }
                .dottxt { 
                    background: #fff; box-shadow: 0 0 5px #333; 
                    padding: 15px; opacity: 0; transition: all 0.5s;
                    position: absolute; top: 110%; 
                }
                &:hover { border: 2px solid rgba(255,255,255,0.6);}
                &:hover > span { width: 10px; height: 10px; }
                &:hover > .dottxt { opacity: 1;}
            }
            
            
            .arti4img1 {
                width: 49%;
                margin-right: 2%;
                position: relative;
                .dotbox1 {
                    position: absolute;
                    top: 50%; left: 65%;
                }
                .dotbox2 {
                    position: absolute;
                    top: 65%; left: 45%;
                }
            }
            .arti4img2 {
                width: 49%; position: relative;
                .dotbox3 {
                    position: absolute;
                    top: 40%; left: 56.5%;
                }
                .dotbox4 {
                    position: absolute;
                    top: 60%; left: 45%;
                }
            }
        } 
    }
`;

const Newitemssection = () => {
    return (
        <NewitemssectionBlock>
            <div className="inner">
                <div className="showmenu">
                    <p>
                        <Link to="/">홈</Link><IoIosArrowForward /><span>신제품</span>
                    </p>
                </div>
                <h2>IKEA 제품에 관한 새로운 소식</h2>
                <h3>여름 분위기에 어울리는 신제품을 만나보세요</h3>
                <p className="pbox"><span>맑고 화창한 날씨가 인테리어를 새롭게 단장하고 싶은 마음이 들게 하죠. IKEA의 신제품을 둘러보고 공간을 더 밝고 가볍게 꾸며보세요.</span><Link to="/">새로운 디자인 아이디어 보러 가기</Link></p>
                <div className="arti4imgbox">
                    <div className="arti4img1">
                            <Link to="/"><img src="images/arti4img01.jpg" alt="" /></Link>
                            <Link to="/">
                                <div className="dotbox1">
                                    <div className="dot"><span></span>
                                        <div className="dottxt">
                                            <h4>VOXLÖV 복슬뢰브</h4>
                                            <p>식탁</p>
                                            <p>￦349,000</p>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                            <Link to="/">
                                <div className="dotbox2">
                                    <div className="dot"><span></span>
                                        <div className="dottxt">
                                            <h4>VOXLÖV 복슬뢰브</h4>
                                            <p>의자</p>
                                            <p>￦99,900</p>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        </div>
                    
                    <div className="arti4img2">
                            <Link to="/"><img src="images/arti4img02.jpg" alt="" /></Link>
                            <Link to="/">
                                <div className="dotbox3">
                                    <div className="dot"><span></span>
                                        <div className="dottxt">
                                            <h4>BEHÖVD 베회브드</h4>
                                            <p>진공 보온병</p>
                                            <p>￦7,000</p>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                            <Link to="/">
                                <div className="dotbox4">
                                    <div className="dot"><span></span>
                                        <div className="dottxt">
                                            <h4>FRÖJERED 프뢰예레드</h4>
                                            <p>서랍앞판</p>
                                            <p>￦50,000</p>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        </div>
                </div>
            </div>
        </NewitemssectionBlock>
    );
};

export default Newitemssection;