import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import cn from 'classnames';
import { AiOutlineMenu, AiOutlineUser, AiOutlineShopping, AiOutlineSearch, AiOutlineCamera } from "react-icons/ai";
import { FiInfo  } from "react-icons/fi";
import { HiOutlinePhone } from "react-icons/hi";
import { MdClose } from "react-icons/md";
import { IoIosArrowUp } from "react-icons/io";

const HeaderBlock = styled.div`
    position:relative;
    .topmenu {
        background:#000;
        display: flex; justify-content:center;
        color:#fff; font-size:12px;
        padding:10px;
        p { 
            padding: 0 30px; 
            .topicon { font-size:16px; vertical-align:middle; }
            span { vertical-align:middle; padding-left:7px; }
        }
    }
    .outer {
        position:fixed; top:40px; left:0; width:100%; z-index:9;
        transition:all 0.5s;
        .inner {
            padding-top: 20px; padding-bottom: 20px;
            display:flex; font-size: 20px;
            justify-content:space-between; align-items:center;
            .navbox { 
                display:flex; align-items:center;
                button {
                    margin-right:30px; transition:all 0.5s;
                    font-size: 20px; background:rgba(255,255,255,0.5); border-radius:30px; padding: 10px 10px 5px ;
                    &:hover { background: #eee; border-radius:30px;  }
                }
                
            }
            .search { 
                width: 65%; background: #fff; box-shadow: 0 0 5px #ddd; height:50px; 
                border-radius:50px; display:flex; justify-content:space-between; padding:10px 20px;
                align-items:center;
                input { width: 90%; border:none; outline:none; font-size: 16px; }
            }
            .menuR { cursor:pointer; .menuRicon { margin-left:20px; } }
            
        }
    }
    .navbg { 
        position:fixed; top:0; left:0; bottom:0; right:0;
        background: rgba(0, 0, 0, 0); z-index:-1; transition:all 0.5s;
        &.opener { background: rgba(0, 0, 0, 0.4); z-index:10; }
    }
    .nav { 
        position:fixed; top:0; left:-480px; bottom:0; width:480px;
        background:#fff; padding:50px;
        font-size:20px; transition:all 0.5s;
        &.opener { left:0px; z-index:11; }
        .navbox { 
            display: flex; justify-content:space-between; align-items:center; 
            .close { 
                width:30px; height:30px; padding: 5px; 
                background: #fff; border-radius:30px; transition:all 0.5s;
                &:hover { background: #eee; border-radius:30px;  } 
            } 
        }
        .depth1 { 
            padding-top:50px;
            line-height:3;
            font-size:15px;
            font-weight: 600;
            li:nth-child(1),
            li:nth-child(2) { font-size:36px;  line-height:2; }
            li:nth-child(5) { padding-bottom:40px; }
            li:nth-child(6),
            li:nth-child(7),
            li:nth-child(8) { font-weight: 300; }
        }
    }
    .gotop {
        position:fixed; bottom: 50px; left: 100px;
        transition:all 0.5s;
        width:46px; height:44px;
        &:hover { width:144px; span { opacity:1;}}
        button { 
            background: transparent; color:#fff; padding:10px;
            font-size:24px;
            border-radius:50px; position:relative;
            span { 
                font-size: 12px; opacity:0;  
                position:absolute; top:14px; left:45px; width:80px;
            }
        }
    }

`;

const Header = () => {

    const [opener , setOpener] = useState(false);
    const open = () => {
        setOpener(true);
    };
    const close = () => {
        setOpener(false);
    };

    const [scy, setScy] = useState(0)
    const onScroll = () => {
        setScy(window.scrollY) 
    }
    useEffect( ()=> {
        window.addEventListener("scroll",onScroll)
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    const gototop = () => {
        window.scroll({
            top: 0,
            behavior: "smooth", 
          });
    }

    return (
        <HeaderBlock>
            <div className="topmenu" style={{ display: (scy) ? "none" : "flex" }}>
                <p><FiInfo className="topicon" /> <span>코로나19 대응 공지</span></p>
                <p><HiOutlinePhone className="topicon" /> <span>비대면 전화주문 서비스</span></p>
                <p><AiOutlineShopping className="topicon" /> <span>IKEA 베스트셀러</span></p>
            </div>
            <div className="outer" style={{ background: (scy) ? "#fff" : "transparent", top: (scy) ? "0px" : "40px"   }}>
                <div className="inner">
                    <div className="navbox">
                        <button onClick={open}><AiOutlineMenu className="ham" /></button>
                        <h1 className="logo" >
                            <Link to="/"><img src="images/logo.png" alt="" /></Link>
                        </h1>
                    </div>
                    <div className="search">
                        <AiOutlineSearch className="searchicon" />
                        <input type="text" placeholder="검색어를 입력하세요" />
                        <AiOutlineCamera className="cameraicon" />
                    </div>
                    <div className="menuR">
                        <Link to="/login"><AiOutlineUser className="menuRicon" /></Link>
                        <Link to="/login"><AiOutlineShopping className="menuRicon" /></Link>
                    </div>
                </div>
            </div>

            
            <div className={cn("navbg", {opener} )}></div>
            <div className={cn("nav", {opener} )}>
                <div className="navbox">
                    <h1 className="logo">
                        <Link to="/"><img src="images/logo.png" alt="" /></Link>
                    </h1>
                    <MdClose className="close" onClick={close} />
                </div>
                <ul className="depth1">
                    <li><Link to="/product">모든 제품</Link></li>
                    <li><Link to="/showroom">디지털 쇼룸</Link></li>
                    <li><Link to="/sale">할인 중</Link></li>
                    <li><Link to="/newitems">신제품</Link></li>
                    <li><Link to="/idea">아이디어</Link></li>
                    <li><Link to="/store">매장안내</Link></li>
                    <li><Link to="/family">IKEA FAMILY</Link></li>
                    <li><Link to="/login">내 프로필</Link></li>
                </ul>
            </div>

            <div className="gotop" style={{ background: (scy) ? "#000" : "transparent", borderRadius: "50px" }} onClick={gototop}>
                <button><IoIosArrowUp /><span>맨 위로 이동</span></button>
            </div>
        </HeaderBlock>
    );
};

export default Header;