import React from 'react';
import styled from 'styled-components';

const ProductitemBlock = styled.div`
    float:left; width: 25%; padding: 0 7px 50px; 
    h3 { font-weight:300; padding-top: 15px; }
    
`;

const Productitem = ({photo, title}) => {
    return (
        <ProductitemBlock>
            <div><img src={photo} alt={title} /><h3>{title}</h3></div>
        </ProductitemBlock>
    );
};

export default Productitem;