import React, { useState,useCallback } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import Productsectiondetail from './Productsectiondetail'
import { IoIosArrowForward  } from "react-icons/io";


const ProductsectionBlock = styled.div`
    h2 { padding: 60px 0 40px; font-size: 24px; font-weight: 600; }
    padding:100px 0;
    .inner {
        font-weight:300; 
        .salemenu { 
            font-size:14px;
            padding-bottom:15px; border-bottom:1px solid #ddd;
            a { padding-right:10px; &:hover { text-decoration:underline; } }
            span { padding-left:10px; }
        }
    }
`;


const Productsection = () => {

    const [activeTab, setActiveTab] = useState(0)
    const onSelect = useCallback(
        (id) => {
            setActiveTab(id)
        },[]
    )

    const [opener , setOpener] = useState(0)
    const onOpen = useCallback(
        (id) => {
            setOpener(id)
        },[]
    )

    return (
        <ProductsectionBlock>
            <div className="inner">
                <div className="salemenu">
                    <p>
                        <Link to="/">홈</Link><IoIosArrowForward /><span>제품</span>
                    </p>
                </div>
                <div className="ProductMenu">
                    <h2>제품</h2> 
                    <Productsectiondetail onSelect={onSelect} activeTab={activeTab} onOpen={onOpen} opener={opener}  />
                </div>
                
            </div>
        </ProductsectionBlock>
    );
};

export default Productsection;