import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const HomeideaItemBlock = styled.div`
    width:33.3%; padding-right:20px;
    position:relative; 
    .hitdotbox {
        position: absolute; top: 50%; left: 50%; transform: translate(-50%,-50%);
        .hitdot { 
            background: rgba(0,0,0,0.4); opacity:0;
            width: 35px; height: 35px; border-radius: 50%;
            position: relative; transition: all 0.5s;
            border: 2px solid rgba(255,255,255,0);
            span { 
                background: #fff; 
                width: 14px; height: 14px; border-radius: 50%;
                box-shadow: 0 0 5px #333;
                position: absolute; top: 50%; left: 50%; transform: translate(-50%,-50%);
            }
            .hitdottxt { 
                background: #fff; box-shadow: 0 0 5px #333; 
                padding: 12px; opacity: 0; transition: all 0.5s;
                position: absolute; top: 110%; width:207px;
            }
            &:hover { border: 2px solid rgba(255,255,255,0.6);}
            &:hover > span { width: 10px; height: 10px; }
            &:hover > .hitdottxt { opacity: 1;}
        }
    }
    &:hover .hitdot { opacity:1; }
`;

const HomeideaItem = ({  name, photo, photo1, photo2, descript, price, title, explain }) => { 
    return (
        <HomeideaItemBlock>
            <Link to={{pathname:'/ideadetail', state:{photo, photo1, photo2, name, descript, price, title, explain}}}> 
                <img src={photo} alt={name} />
                <div className="hitdotbox">
                    <div className="hitdot"><span></span>
                        <div className="hitdottxt">
                            <h3>{name}</h3>
                            <p>{descript}</p>
                         <p>{price}</p>
                        </div>
                    </div>
                </div>
            </Link> 
            
        </HomeideaItemBlock>
    );
};

export default HomeideaItem;