import React, { useRef } from 'react'; 
import { Link } from 'react-router-dom';
import './Salesection.scss'; 
import { IoIosArrowForward  } from "react-icons/io";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import furniture from './funiture.json';
import FunitureItem from './FunitureItem';

import {FiArrowRight, FiArrowLeft} from "react-icons/fi"; 



const Salesection = () => {
    const settings = {
        dots:true,
        infinite:true, 
        speed:600,
        autoplay:false,
        autoplaySpeed:4000, 
        slidesToShow:4,
        slidesToScroll:4,
        prevArrow: <FiArrowLeft />, 
        nextArrow: <FiArrowRight />
    } 
    const section1Ref = useRef(null);
    const section2Ref = useRef(null);
    const section3Ref = useRef(null);
    const section4Ref = useRef(null);
  
    const scrollTo = (ref) => {
      window.scroll({
        top: ref.current.offsetTop,
        behavior: "smooth", 
      });
    };


    return (
        <div className="salesection">
            <div className="inner">
                <div className="salemenu">
                    <p>
                        <Link to="/">홈</Link><IoIosArrowForward /><span>할인 중</span>
                    </p>
                </div>
                <div className="salecont1" ref={section1Ref} style={{ height: "100%", margin: 0 }}>
                    <div className="saletop">
                        <h2>할인 중</h2>
                        <p>거리 두기와 함께 멀어진 설렘, 지금 IKEA와 함께 찾아봐요! 매일 아침 나를 감싸는 침구 한 장의 포근함, 깔끔하게 정리된 옷들을 보고 ‘오늘은 무슨 옷을 입을까?’ 고민하는 순간, 침실에서의 모든 순간이 설레게 될 거에요.</p>
                        <p>
                            <button onClick={() => scrollTo(section1Ref)}>침실가구 및 액세서리 최대 15% 할인</button><span>|</span>
                            <button onClick={() => scrollTo(section2Ref)}>맘편한 푸드바우처 증정</button><span>|</span>
                            <button onClick={() => scrollTo(section3Ref)}>배송비 할인</button><span>|</span>
                            <button onClick={() => scrollTo(section4Ref)}>초콜릿 증정</button>
                        </p>
                    </div>
                    <div className="svbox">
                        <div className="salevideo">
                            <iframe width="788" height="433" src="https://www.youtube.com/embed/17SonGgZ88U" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                        </div>
                        <div className="saletxt">
                            <h3>설레는 우리집,<br /> 500여개 침실가구 및 액세서리 최대 15% 할인</h3>
                            <p>행사 기간: 5월 6일 - 5월 19일</p>
                            <p>하루를 시작하고 마무리 하는 공간인 침실에서 IKEA 제품들로 설렘을 더해보는 건 어때요?</p>
                            <p><span>IKEA Family</span>라면 지금 500여개의 침실가구 및 액세서리를 최대 15% 할인된 가격으로 만날 수 있어요.</p>
                        </div>
                    </div>
                    <div className="salelist">
                        <h2>침실 가구</h2>
                        <Slider {...settings}>
                            {
                                furniture.map( (data, index) => <FunitureItem className="slide" key={index} name={data.name} photo={data.photo} photo2={data.photo2} description={data.description} price={data.price} score={data.score} explain={data.explain} />)
                            }
                        </Slider>
                    </div>
                    <div className="saleul">
                        <h3>[이용약관]</h3>
                        <ul>
                            <li>본 행사는 5월 6일부터 5월 19일까지 진행되며 IKEA 모든 매장과 IKEA 공식 온라인몰에서 진행됩니다.</li>
                            <li>본 행사는 할인 태그가 달린 일부 제품에 한하여 15% 할인 적용 가능합니다. IKEA 매장과 IKEA 공식 온라인몰 전 제품이 아닌 일부 제품으로 한정됩니다.</li>
                            <li>본 행사의 혜택은 1일 1회에 한하며, 1인당 제품별 최대 10개 구매 제한 있습니다. 매장과 온라인 재고 상황에 따라 구매 제한 수량이 다를 수 있습니다.</li>
                            <li>본 행사에 포함된 혜택 내용은 ‘행사 기간’ 동안 제품을 구매하는 경우에만 유효합니다.</li>
                            <li>본 행사는 IKEA FAMILY 멤버에게만 제공되며, IKEA 공식 온라인몰에서 무료가입 가능합니다.</li>
                            <li>재고 소진 제품에 대해 별도로 공지하지 않으며, 소진 시 행사가 중단될 수 있습니다. 예약 구매는 불가능하며, 재입고 여부는 고객 지원 센터 문의 전화 등으로 확인이 어렵습니다.</li>
                            <li>일부 제품은 조립이 필요할 수 있으며, 조립 및 배송 서비스 비용에는 할인 조건이 적용되지 않습니다.</li>
                            <li>안내된 할인 조건(대상 제품, 할인율, 기간 및 수량 등)은 IKEA 내부 정책 및 사정에 의해 사전 공지 없이 변경 또는 철회될 수 있습니다.</li>
                            <li>본 행사에 해당하는 제품의 교환 또는 환불 시에 구매 영수증을 반드시 제시해야 가능합니다.</li>
                        </ul> 
                    </div>
                </div>
                
                <div className="salecont2" ref={section2Ref} style={{ height: "100%", margin: 0 }}>
                    <h2>설레는 우리집, 맘편한 푸드바우처 증정</h2>
                    <span className="h2span">행사 기간: 4월 22일 - 5월 19일</span>
                    <div className="salecontent">
                        <div className="saleimg">
                            <img src="images/saleimg01.jpg" />
                        </div>
                        <div className="saletxt2">
                            <p>어린이 IKEA 제품을 150,000원 이상구매 시, 다음 날부터 사용이 가능한 15,000원 푸드 바우처를 드려요.</p>
                            <p>우리 아이를 위한 제품을 구매하고 아이와 행복도 함께 맛보세요.</p>
                            <p>* 본 행사는 전국 오프라인 매장 (광명/고양/기흥/동부산점)에서만 진행됩니다.</p>
                        </div>
                    </div>
                    <div className="saleul">
                        <h3>[이용약관]</h3>
                        <ul>
                            <li>본 행사는 4월 22일부터 5월 19일까지 IKEA 모든 매장(온라인 제외)에서 진행됩니다.</li>
                            <li>어린이 IKEA 제품 15만 원 이상 구매 시 이케아 레스토랑 15,000원 할인 쿠폰을 증정합니다.</li>
                            <li>본 행사는 IKEA 매장 전 제품이 아닌 어린이 IKEA 제품으로만 한정합니다.</li>
                            <li>본 행사는 당일에 한하여 1인 1회 증정 가능하며, 구매 영수증 합산 및 분할은 불가능합니다.</li>
                            <li>증정된 레스토랑 쿠폰은 발급일 다음 날부터 2021/6/30까지 사용 가능하며, 레스토랑에서 2만 원 이상 구매 시 15,000원 할인 적용 가능합니다.</li>
                            <li>레스토랑 쿠폰 분실 및 파손 시 재발급은 불가합니다. 쿠폰은 1인 1회 사용 가능합니다.</li>
                            <li>본 행사를 통해 증정된 쿠폰은 결제 전 제시 시 사용 가능합니다.</li>
                            <li>레스토랑 쿠폰은 현금 교환 및 잔액 환불이 불가능합니다.</li>
                            <li>본 행사는 IKEA FAMILY 멤버에게만 제공되며, IKEA 공식 온라인몰에서 무료가입 가능합니다.</li>
                            <li>본 행사에 포함된 혜택 내용은 ‘행사 기간’ 동안 매장 재고 제품을 구매하는 경우에만 유효합니다. (온라인 재고 구매 시 행사 적용 불가능합니다). 매장 재고 사정에 따라 오퍼 내용이 일부 다르게 적용될 수 있습니다.</li>
                            <li>일부 제품은 조립이 필요할 수 있으며, 조립 및 배송 서비스 비용에는 할인 조건이 적용되지 않습니다.</li>
                            <li>안내된 할인 조건(대상 제품, 할인율, 기간 및 수량 등)은 IKEA 내부 정책 및 사정에 의해 사전 공지 없이 변경 또는 철회될 수 있습니다.</li>
                            <li>본 행사에 해당하는 제품의 교환 또는 환불 시에 구매 영수증을 반드시 제시해야 가능합니다.</li>
                        </ul> 
                    </div>
                </div>

                <div className="salecont3" ref={section3Ref} style={{ height: "100%", margin: 0 }}>
                    <h2>내 방에 필요한 모든 가구 전부 담아도 배송비 39,000원!</h2>
                    <span className="h2span">행사 기간: 3월 31일 - 5월 31일</span>
                    <div className="salecontent">
                        <div className="saleimg">
                            <img src="images/saleimg02.jpg" />
                        </div>
                        <div className="saletxt2">
                            <p>모든 매장, 온라인몰, IKEA 앱에서 구매 금액에 상관없이 가구 배송비 <span>39,000원</span>의 혜택을 받아보세요! (제주도 별도 요금 적용)</p>
                            <p>띵동! 반가운 택배와 함께 찾아오는 설레는 하루를 만들어보세요.</p>
                            <ul>
                                <li><span>온라인 가구 배송비:</span> 59,000원 <span>- 39,000원</span></li>
                                <li><span>매장 가구 배송비:</span> 49,000원 <span>- 39,000원</span></li>
                                <li>매장 인근 근거리 배송은 29,000원입니다.</li>
                                <li><span>제주도 온라인 배송비: 109,000원 - 75,000원</span></li>
                            </ul>
                            <p>* 5 세제곱 미터 (양문형 냉장고 2개 반 정도의 부피)까지 해당 요금으로 가능합니다.</p>
                        </div>
                    </div>
                    <div className="saleul">
                        <h3>[이용약관]</h3>
                        <ul>
                            <li>본 행사는 3월 31일부터 5월 31일까지 IKEA 모든 매장 및 공식 온라인몰에서 진행됩니다.</li>
                            <li>당일 구매 건에 한하여 39,000원으로 배송비가 적용됩니다.</li>
                            <li>매장 인근 지역의 근거리 배송 서비스 비용은 29,000원으로 적용되며 매장마다 근거리 배송 서비스 지역은 상이합니다.</li>
                            <li>최대 5세제곱미터까지(양문형 냉장고 2개 반 정도의 부피) 39,000원의 배송비가 적용됩니다. (제주도의 경우 75,000원) 5세제곱미터 이상의 경우 실제 구매 제품의 부피에 따라 대량 구매 배송비용이 적용됩니다.</li>
                            <li>택배, 픽킹 서비스 그리고 매장 픽업 서비스(클릭 앤 콜렉트)는 할인 적용 대상에서 제외됩니다.</li>
                            <li>배송서비스는 매장주문건과 온라인주문건은 합배송이 불가능합니다.</li>
                            <li>배송료 할인은 단순 고객 변심으로 인한 교환 및 환불 배송에는 적용되지 않으며, 일반 배송 요금이 적용됩니다(온라인 : 59,000원, 오프라인매장 : 49,000원)제품 출고 이후 배송 취소 요청 시에는 반품비 29,000원이 부과됩니다.</li>
                            <li>본 행사에 포함된 혜택 내용은 ‘행사 기간’ 동안 배송하는 경우에만 유효합니다.</li>
                            <li>안내된 할인 조건(대상 제품, 할인율, 기간 및 수량 등)은 IKEA 내부 정책 및 사정에 의해 사전 공지 없이 변경 또는 철회될 수 있습니다.</li>
                        </ul> 
                    </div>
                </div>

                <div className="salecont4" ref={section4Ref} style={{ height: "100%", margin: 0 }}>
                    <h2>설레는IKEA, 초콜릿 증정</h2>
                    <span className="h2span">행사 기간: 4월 22일 - 5월 19일</span>
                    <div className="salecontent">
                        <div className="saleimg">
                            <img src="images/saleimg03.jpg" />
                        </div>
                        <div className="saletxt2">
                            <p>IKEA 비스트로에서 딸기아이스크림 구매 시 주사기 모양의 초콜렛과 함께 드려요. 달콤한 초콜릿과 함께 행복한 힐링을 시작해보세요!</p>
                            <p>* 본 행사는 전국 오프라인 매장 (광명/기흥/동부산점)에서만 진행됩니다.</p>
                        </div>
                    </div>

                </div>            

            </div>
        </div>
    );
};

export default Salesection;