import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { IoIosArrowForward  } from "react-icons/io";
import {FiArrowRight} from "react-icons/fi"; 


const ShowroomsectionBlock = styled.div`
    padding:100px 0;
    .inner {
        font-weight:300; 
        .showmenu { 
            font-size:14px;
            padding-bottom:15px; border-bottom:1px solid #ddd;
            a { padding-right:10px; &:hover { text-decoration:underline; } }
            span { padding-left:10px; }
        }
        .showcontent {
            h2 { padding: 60px 0 40px; font-size: 36px; font-weight: 600; }
            .showimgbox {
                display:flex; flex-wrap:wrap;
                .showimg {
                    width:32%; margin: 0 1.33% 1.33% 0;;
                    position:relative;
                    &:hover {
                        .showtxt { opacity:1 }
                        &::before { opacity:1 }
                    }
                    .showtxt { 
                        opacity:0; transition:all 0.5s;
                        position:absolute; top:50%; left:50%; 
                        transform:translate(-50%,-50%);
                        font-size: 20px; color:#fff;
                        h3 { display:flex; align-items:center; background:rgba(0,0,0,0.7); border-radius:50px; padding:7px 15px;}
                    }
                    &::before { 
                        content:''; opacity:0;  transition:all 0.5s;
                        background: rgba(0,0,0,0.4);
                        position:absolute; top:0%; left:0%; 
                        width:100%; height:100%; 
                    } 
                }
            }
        }
    }
`;



const Showroomsection = () => {
    return (
        <ShowroomsectionBlock>
            <div className="inner">
                <div className="showmenu">
                    <p>
                        <Link to="/">홈</Link><IoIosArrowForward /><span>디지털쇼룸</span>
                    </p>
                </div>
                <div className="showcontent">
                    <h2>디지털쇼룸</h2>
                    <div className="showimgbox">
                        <div className="showimg">
                            <img src="images/showroom01.jpg" alt="" />
                            <div className="showtxt">
                                <h3>침실 <FiArrowRight /></h3>
                            </div>
                        </div>
                        <div className="showimg">
                            <img src="images/showroom02.jpg" alt="" />
                            <div className="showtxt">
                                <h3>거실 <FiArrowRight /></h3>
                            </div>
                        </div>
                        <div className="showimg">
                            <img src="images/showroom03.jpg" alt="" />
                            <div className="showtxt">
                                <h3>주방 <FiArrowRight /></h3>
                            </div>
                        </div>
                        <div className="showimg">
                            <img src="images/showroom04.jpg" alt="" />
                            <div className="showtxt">
                                <h3>다이닝 <FiArrowRight /></h3>
                            </div>
                        </div>
                        <div className="showimg">
                            <img src="images/showroom05.jpg" alt="" />
                            <div className="showtxt">
                                <h3>아이방 <FiArrowRight /></h3>
                            </div>
                        </div>
                        <div className="showimg">
                            <img src="images/showroom06.jpg" alt="" />
                            <div className="showtxt">
                                <h3>욕실 <FiArrowRight /></h3>
                            </div>
                        </div>
                        <div className="showimg">
                            <img src="images/showroom07.jpg" alt="" />
                            <div className="showtxt">
                                <h3>홈오피스 <FiArrowRight /></h3>
                            </div>
                        </div>
                        <div className="showimg">
                            <img src="images/showroom08.jpg" alt="" />
                            <div className="showtxt">
                                <h3>현관 <FiArrowRight /></h3>
                            </div>
                        </div>
                        <div className="showimg">
                            <img src="images/showroom09.jpg" alt="" />
                            <div className="showtxt">
                                <h3>야외용품 <FiArrowRight /></h3>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </ShowroomsectionBlock>
    );
};

export default Showroomsection;