import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { IoIosArrowForward  } from "react-icons/io";

const FamilysectionBlock = styled.div`
    padding:100px 0;
    .inner {
        font-weight:300; 
        .familymenu { 
            font-size:14px;
            padding-bottom:15px; border-bottom:1px solid #ddd;
            a { padding-right:10px; &:hover { text-decoration:underline; } }
            span { padding-left:10px; }
        }
        h2 { font-size:36px; padding: 60px 0 30px; }
        .familyouter {
            display:flex;
            .familycont { 
                width: 50%; padding-right:20px;
                h3 { font-size:25px; padding: 30px 0 15px;}
                button {
                    display:block; font-weight:600; padding: 12px 30px; 
                    border-radius:50px; font-family:inherit;
                    margin-top:35px; transition:all 0.5s;
                    background: #000; color:#fff; &:hover{ background: #333; }
                }
            }
        }
    }
`;

const Familysection = () => {
    return (
        <FamilysectionBlock>
            <div className="inner">
                <div className="familymenu">
                    <p>
                        <Link to="/">홈</Link><IoIosArrowForward /><span>IKEA Family</span>
                    </p>
                </div>
                <h2>IKEA Family에 오신 것을 환영합니다.</h2>
                <div className="familyouter">
                    <div className="familycont">
                        <img src="images/family01.jpg" alt="family" />
                        <div className="familytxt">
                            <h3>IKEA Family 에서는 여러분의 아이디어가 현실이 됩니다.</h3>
                            <p>가입이 까다로운 멤버십 클럽도 있지만, IKEA Family는 달라요. 누구에게나 열려 있습니다. 이제 막 집 꾸미기에 관심을 가진 초보부터 셀프 인테리어의 전문가까지 누구나 환영이에요. IKEA Family 멤버에게는 제품 할인, 무료 교환, 워크숍, 평일 무료 커피 등 다양한 혜택을 제공해 드립니다.깜짝 선물도 기다리고 있어요. IKEA에 보내주신 사랑에 늘 보답하겠습니다.</p>
                            <button>IKEA Family 가입하기</button>
                            <button>내 계정으로 로그인하기</button>
                            <button>멤버십 카드</button>
                        </div>
                    </div>
                    <div className="familycont">
                        <img src="images/family02.jpg" alt="family" />
                        <div className="familytxt">
                            <h3>IKEA Family with 신한카드</h3>
                            <p>IKEA를 사랑해주시는 IKEA Family에게 조금 더 나은 혜택과 서비스를 제공하고자 준비했어요. 더 다양해진 결제 옵션을 지금 바로 확인해 보세요!</p>
                            <button>자세히 보기</button>
                        </div>
                    </div>
                </div>
            </div>
        </FamilysectionBlock>
    );
};

export default Familysection;