import React from 'react';
import Header from './Header';
import Footer from './Footer';
import styled from 'styled-components'; 

const MemberTempBlock = styled.div`
    
`;

const MemberTemp = ({children}) => {
    return (
        <div>
            <Header />
            <MemberTempBlock>
                
                    { children }
                
            </MemberTempBlock>
            <Footer />
        </div>
    );
};

export default MemberTemp;