import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { IoIosArrowForward  } from "react-icons/io";

const StoresectionBlock = styled.div`
    padding:100px 0;
    .inner {
        font-weight:300; 
        .storemenu { 
            font-size:14px;
            padding-bottom:15px; border-bottom:1px solid #ddd;
            a { padding-right:10px; &:hover { text-decoration:underline; } }
            span { padding-left:10px; }
        }
        h2 { font-size:36px; padding: 60px 0 30px; }
        .studio {
            h3 { font-size:20px; padding-bottom:10px; }
            p { width:60%;  }
            .studiobox {
                padding:60px 0; display:flex; 
                width:100%; flex-wrap:wrap;
                .studiocont {
                    width:50%;  
                    padding-right:20px;
                    .studiotxt {
                        padding:40px 0; line-height:2.5;
                        ul { 
                            list-style: disc inside; 
                        }
                        button {
                            display:block; font-weight:600; padding: 12px 30px; 
                            border-radius:50px; font-family:inherit;
                            margin-top:35px; transition:all 0.5s;
                        }
                        button.btnbk { background: #000; color:#fff; &:hover{ background: #333; }}
                        button.btnwh { background: #fff; color:#000; border:1px solid #eee; &:hover{ border:1px solid #aaa; } }
                    }
                }
            }
        }
        
    }
`;


const Storesection = () => {
    return (
        <StoresectionBlock>
            <div className="inner">
                <div className="storemenu">
                    <p>
                        <Link to="/">홈</Link><IoIosArrowForward /><span>매장 안내</span>
                    </p>
                </div>
                <h2>매장 안내</h2>
                <div className="studio">
                    <h3>플래닝 스튜디오</h3> 
                    <p>플래닝 스튜디오에서는 1:1 무료상담을 통해 편리하게 가구 플래닝 상담을 받고 원하는 날짜에 배송과 조립서비스까지 한번에 신청할 수 있습니다. 서울에 위치한 현대백화점 신도림 및 천호에서 만나요!</p>
                    <div className="studiobox">
                        <div className="studiocont">
                            <img src="images/store01.jpg" alt="store" />
                            <div className="studiotxt">
                                <h4>IKEA 플래닝 스튜디오 신도림</h4>
                                <p>신도림 현대백화점 디큐브시티 지하 1층</p>
                                <p>(서울시 구로구 경인로 662)</p>
                                <h5>운영 시간</h5>
                                <ul>
                                    <li>월요일 ~ 목요일 11 : 00 ~ 21 : 00</li>
                                    <li>금요일 ~ 일요일 11 : 00 ~ 21 : 30</li>
                                </ul>
                                <button className="btnbk">IKEA 플래닝 스튜디오 예약하기</button>
                                <button className="btnwh">IKEA 플래닝 스튜디오 바로가기</button>
                            </div>
                        </div>
                        <div className="studiocont">
                            <img src="images/store02.jpg" alt="store" />
                            <div className="studiotxt">
                                <h4>IKEA 플래닝 스튜디오 천호</h4>
                                <p>현대백화점 천호점 9층</p>
                                <p>(서울시 강동구 천호대로 1005)</p>
                                <h5>운영 시간</h5>
                                <ul>
                                    <li>월요일 ~ 목요일 10 : 30 ~ 20 : 00</li>
                                    <li>금요일 ~ 일요일 10 : 30 ~ 20 : 30</li>
                                </ul>
                                <button className="btnbk">IKEA 플래닝 스튜디오 예약하기</button>
                                <button className="btnwh">IKEA 플래닝 스튜디오 바로가기</button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="studio">
                    <h3>전국 IKEA 매장</h3> 
                    <p>IKEA에서 운영하는 매장들은 매장 별로 이벤트나 프로모션을 운영하고 있습니다. 아래에서 방문을 원하는 매장을 찾아 다양한 매장별 프로모션 및 이벤트 정보를 만나보세요.</p>
                    <div className="studiobox">
                        <div className="studiocont">
                            <img src="images/store03.jpg" alt="store" />
                            <div className="studiotxt">
                                <h4>IKEA 기흥점</h4>
                                <p>경기도 용인시 기흥구 신고매로 62</p>
                                <h5>매장</h5>
                                <ul>
                                    <li>10 : 00 ~ 22 : 00</li>
                                </ul>
                                <h5>* 임시 운영 일정 (정부 시행 지침 기간과 동일)<br />매장</h5>
                                <ul>
                                    <li>월요일 - 목요일 10 : 00 ~ 21 : 00</li>
                                    <li>금요일 - 일요일 10 : 00 ~ 22 : 00</li>
                                </ul>
                                <h5>레스토랑</h5>
                                <ul>
                                    <li>월요일 - 목요일 09 : 30 ~ 20 : 30<br />(마지막 주문 20:00)</li>
                                    <li>금요일 - 일요일 09 : 30 ~ 21 : 30<br />(마지막 주문 21:00)</li>
                                </ul>
                                <button className="btnbk">기흥점 바로가기</button>
                                <button className="btnwh">지도 확인하기</button>
                            </div>
                        </div>
                        <div className="studiocont">
                            <img src="images/store04.jpg" alt="store" />
                            <div className="studiotxt">
                                <h4>IKEA 광명점</h4>
                                <p>경기도 광명시 일직로 17</p>
                                <h5>매장</h5>
                                <ul>
                                    <li>10 : 00 ~ 22 : 00</li>
                                </ul>
                                <h5>* 임시 운영 일정 (정부 시행 지침 기간과 동일)<br />매장</h5>
                                <ul>
                                    <li>월요일 - 목요일 10 : 00 ~ 21 : 00</li>
                                    <li>금요일 - 일요일 10 : 00 ~ 22 : 00</li>
                                </ul>
                                <h5>레스토랑</h5>
                                <ul>
                                    <li>월요일 - 목요일 09 : 30 ~ 20 : 30<br />(마지막 주문 20:00)</li>
                                    <li>금요일 - 일요일 09 : 30 ~ 21 : 30<br />(마지막 주문 21:00)</li>
                                </ul>
                                <button className="btnbk">광명점 바로가기</button>
                                <button className="btnwh">지도 확인하기</button>
                            </div>
                        </div>
                        <div className="studiocont">
                            <img src="images/store05.jpg" alt="store" />
                            <div className="studiotxt">
                                <h4>IKEA 고양점</h4>
                                <p>경기도 고양시 덕양구 권율대로 420</p>
                                <h5>매장</h5>
                                <ul>
                                    <li>10 : 00 ~ 22 : 00</li>
                                </ul>
                                <h5>* 임시 운영 일정 (정부 시행 지침 기간과 동일)<br />매장</h5>
                                <ul>
                                    <li>월요일 - 목요일 10 : 00 ~ 21 : 00</li>
                                    <li>금요일 - 일요일 10 : 00 ~ 22 : 00</li>
                                </ul>
                                <h5>레스토랑</h5>
                                <ul>
                                    <li>월요일 - 목요일 09 : 30 ~ 20 : 30<br />(마지막 주문 20:00)</li>
                                    <li>금요일 - 일요일 09 : 30 ~ 21 : 30<br />(마지막 주문 21:00)</li>
                                </ul>
                                <button className="btnbk">고양점 바로가기</button>
                                <button className="btnwh">지도 확인하기</button>
                            </div>
                        </div>
                        <div className="studiocont">
                            <img src="images/store06.jpg" alt="store" />
                            <div className="studiotxt">
                                <h4>IKEA 동부산점</h4>
                                <p>부산광역시 기장군 기장읍 동부산관광3로 17</p>
                                <h5>매장</h5>
                                <ul>
                                    <li>10 : 00 ~ 22 : 00</li>
                                </ul>
                                <h5>* 임시 운영 일정 (정부 시행 지침 기간과 동일)<br />매장</h5>
                                <ul>
                                    <li>월요일 - 목요일 10 : 00 ~ 21 : 00</li>
                                    <li>금요일 - 일요일 10 : 00 ~ 22 : 00</li>
                                </ul>
                                <h5>레스토랑</h5>
                                <ul>
                                    <li>월요일 - 목요일 09 : 30 ~ 20 : 30<br />(마지막 주문 20:00)</li>
                                    <li>금요일 - 일요일 09 : 30 ~ 21 : 30<br />(마지막 주문 21:00)</li>
                                </ul>
                                <button className="btnbk">동부산점 바로가기</button>
                                <button className="btnwh">지도 확인하기</button>
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
        </StoresectionBlock>
    );
};

export default Storesection;