import React, {useState,useCallback} from 'react';
import { Link } from 'react-router-dom';
import cn from 'classnames';
import './Homesection.scss';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import furniture from './funiture.json';
import FunitureItem from './FunitureItem';
import Homeidea from './Homeidea';

import {FiArrowRight, FiArrowLeft} from "react-icons/fi"; 

   



const Headersection = () => {

    const [move, setMove] = useState(false); 
    const moveRight = () => {
        setMove( true );
    }; 
    const moveLeft = () => {
        setMove( false );
    };
    const [activeTab, setActiveTab] = useState(0)
    const onSelect = useCallback(
            (id) => {
                setActiveTab(id)
            },[]
        )

    const settings = {
        dots:true,
        infinite:true,
        speed:600,
        autoplay:false,
        autoplaySpeed:4000,
        slidesToShow:4,
        slidesToScroll:4,
        prevArrow: <FiArrowLeft />, 
        nextArrow: <FiArrowRight />
    } 

    return (
        <div className="homeWrap"> 
            <div className="article1"> 
                    <div className="main">
                        <div className="image">
                            <Link to="/sale"><img src="images/slideimg01.jpg" alt="" /></Link>
                        </div>
                        <div className="text">
                            <div className="h2box">
                                <h2>
                                    <p>500여개</p>
                                    <p>침실가구 및 액세사리</p>
                                    <p>최대<span>15%</span>OFF</p>
                                </h2>
                            </div>
                            <div className="txtbox">
                                <h3>500여개 침실가구 및 액세서리 최대 15% 할인</h3>
                                <p>KEA Family 를 위한 14일간의 특별한 혜택! 하루를 시작하고 마무리 하는 공간인 침실에 IKEA 제품들로 셀렘을 더해보는 건 어때요? 5월19일까지 500여개의 침실가구 및 액세서리를 최대15% 할인된 가격에 만나보세요!</p>
                                <Link to="/sale">지금 할인 받기</Link>
                            </div>
                        </div>
                    </div>
            </div>
            <div className="article2">
                <div className="inner">
                    <h2>진행 중인 이벤트 및 프로모션</h2>
                    <ul className={cn("cf", {move})}>
                        <li>
                            <Link to="/sale"><img src="images/arti2img01.jpg" alt="" /></Link>
                            <div className="text">
                                <h3>전부 담아도 온라인 가구 배송비 &#8361;39,000</h3>
                                <p>3월31일부터 5월31일까지 최대 &#8361;20,000 할인된 온라인 가구 배송비로 합리적인 쇼핑을 즐겨보세요!</p>
                                <button><Link to="/sale"><FiArrowRight /></Link></button>
                            </div>
                        </li>
                        <li>
                            <Link to="/"><img src="images/arti2img02.jpg" alt="" /></Link>
                            <div className="text text1">
                                <h3>바깥 공기가 반가워지는 계절</h3>
                                <p>다양한 아웃도어 제품으로 야외 공간을 안락하게 꾸며보세요.</p>
                                <button><Link to="/"><FiArrowRight /></Link></button>
                            </div>
                        </li>
                        <li>
                            <Link to="/"><img src="images/arti2img03.jpg" alt="" /></Link>
                            <div className="text text2">
                                <h3>더 낮은 새로운 가격</h3>
                                <p>품질은 그대로 가격은 아래로, 더욱 합리적인 가격으로 돌아왔어요.</p>
                                <button><Link to="/sale"><FiArrowRight /></Link></button>
                            </div>
                        </li>
                        <li>
                            <Link to="/"><img src="images/arti2img04.jpg" alt="" /></Link>
                            <div className="text text3">
                                <h3>NEW IKEA Live</h3>
                                <p>5월13일 오전 10시! IKEA Live가 새롭게 찾아옵니다. 인테리어 디자이너가 직접 추천하는 할인 제품과 함께 홈퍼니싱 팁까지! 꿀팁으로 가득한 IKEA Live 기대해 주세요!</p>
                                <button><Link to="/"><FiArrowRight /></Link></button>
                            </div>
                        </li>
                        <li>
                            <Link to="/"><img src="images/arti2img05.jpg" alt="" /></Link>
                            <div className="text text4">
                                <h3>새로워진 IKEA Lab 성수</h3>
                                <p>더욱 다양해진 지속가증한 이벤트와 새로운 루티의 집을 경험해 보세요!</p>
                                <button><Link to="/"><FiArrowRight /></Link></button>
                            </div>
                        </li>
                    </ul>
                    
                    <button className="Left" onClick={moveLeft} style={{ opacity: (move) ? "1" : "0" }}><FiArrowLeft /></button>
                    <button className="Right" onClick={moveRight} style={{ opacity: (move) ? "0" : "1" }}><FiArrowRight /></button>
                    
                    <div className="bar">
                        <span className={cn("stick",{move})}></span>
                    </div>
                </div>
            </div>
            <div className="article3">
                <div className="inner">
                    <h2>추천 제품</h2>
                    <Slider {...settings}>
                        {
                            furniture.map( (data, index) => <FunitureItem className="slide" key={index} name={data.name} photo={data.photo} photo2={data.photo2} description={data.description} price={data.price} score={data.score} explain={data.explain} />)
                        }
                    </Slider> 
                </div>
            </div>
            <div className="article5"> 
                <div className="inner">
                    <h2>홈퍼니싱 아이디어 더 보기</h2> 
                    <Homeidea onSelect={onSelect} activeTab={activeTab} />
                </div>
            </div>
            <div className="article4">
                <div className="inner">
                    <h2><span>신제품을 만나보세요</span><Link to="/newitems">신제품 보러가기</Link></h2>
                    <div className="arti4imgbox">
                        <div className="arti4img1">
                            <Link to="/newitems"><img src="images/arti4img01.jpg" alt="" /></Link>
                            <Link to="/">
                                <div className="dotbox1">
                                    <div className="dot"><span></span>
                                        <div className="dottxt">
                                            <h3>VOXLÖV 복슬뢰브</h3>
                                            <p>식탁</p>
                                            <p>￦349,000</p>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                            <Link to="/">
                                <div className="dotbox2">
                                    <div className="dot"><span></span>
                                        <div className="dottxt">
                                            <h3>VOXLÖV 복슬뢰브</h3>
                                            <p>의자</p>
                                            <p>￦99,900</p>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        </div>
                        
                        <div className="arti4img2">
                            <Link to="/newitems"><img src="images/arti4img02.jpg" alt="" /></Link>
                            <Link to="/">
                                <div className="dotbox3">
                                    <div className="dot"><span></span>
                                        <div className="dottxt">
                                            <h3>BEHÖVD 베회브드</h3>
                                            <p>진공 보온병</p>
                                            <p>￦7,000</p>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                            <Link to="/">
                                <div className="dotbox4">
                                    <div className="dot"><span></span>
                                        <div className="dottxt">
                                            <h3>FRÖJERED 프뢰예레드</h3>
                                            <p>서랍앞판</p>
                                            <p>￦50,000</p>
                                        </div>
                                    </div>

                                </div>
                            </Link>
                        </div>
                        
                    </div>
                </div>
            </div>
            
        </div>
    );
};

export default Headersection;