import React from 'react';
import { HashRouter, Route } from 'react-router-dom';
import ScrollToTop  from './ScrollToTop';
import Home from './routes/Home';
import Login from './routes/Login';
import Join from './routes/Join';
import Product from './routes/Product';
import Showroom from './routes/Showroom';
import Sale from './routes/Sale';
import Newitems from './routes/Newitems';
import Idea from './routes/Idea';
import Store from './routes/Store';
import Family from './routes/Family';
import Ideadetail from './routes/Ideadetail';
import Furitemdetail from './routes/Furitemdetail';

const App = () => {

 
  return (
    <HashRouter>
      <ScrollToTop>
        <Route path="/" component={Home} exact={true} />
        <Route path="/login" component={Login} />
        <Route path="/join" component={Join} />
        <Route path="/product" component={Product} />
        <Route path="/showroom" component={Showroom} />
        <Route path="/sale" component={Sale} />
        <Route path="/newitems" component={Newitems} />
        <Route path="/idea" component={Idea} />
        <Route path="/store" component={Store} />
        <Route path="/family" component={Family} />
        <Route path="/ideadetail" component={Ideadetail} />
        <Route path="/furitemdetail" component={Furitemdetail} />
      </ScrollToTop>
    </HashRouter>
  );
};

export default App;