import React, {useState} from 'react';
import styled, {css} from 'styled-components';
import Productjson from './Productsection.json';
import Productitem from './Productitem';
import Productitembottom from './Productitembottom';



const ProductsectiondetailBlock = styled.div`
    display:flex;
    .ProductMenu { 
        width:15%; 
        li { 
            font-size: 20px; font-weight:600; 
            padding: 5px 0; cursor:pointer;
            .depth2 {
                // height:0; opacity:0; 
                transition:all 0.5s;
                li { font-size: 16px; font-weight:300; }  
                // &:hover { height:136px; opacity:1; }
            }
        }
    }
    .Productcontent {
        width:85%; 
        h2 { padding: 0 0 20px 7px; }
    
        .h3 { 
            font-size:25px; padding-bottom:20px;
        }


    }
`;
const Menulist = styled.div`
    li {
        ${
            props => props.active && css` color: #0058a3; `
        }
        .depth2 { 
            li {
                ${
                    props => props.active && css` color: #000; `
                }
            }
        }
    }

`;


const tabTit = [
    { id:0, headmenu:"홈스마트", li1:"스마트조명", li2:"전동 블라인드", li3:"리모콘/무선장치", li4:"스마트 시스템", part:Productjson.part1 },
    { id:1, headmenu:"가구", li1:"스마트조명", li2:"전동 블라인드", li3:"리모콘/무선장치", li4:"스마트 시스템", part:Productjson.part2 },
    { id:2, headmenu:"침대/매트리스", li1:"스마트조명", li2:"전동 블라인드", li3:"리모콘/무선장치", li4:"스마트 시스템", part:Productjson.part3 },
    { id:3, headmenu:"홈데코", li1:"스마트조명", li2:"전동 블라인드", li3:"리모콘/무선장치", li4:"스마트 시스템", part:Productjson.part4 }
]



const Productsection = ({onSelect, activeTab}) => {

    // const [opener , setOpener] = useState(false);
    // const open = () => {
    //     setOpener( !opener );
    // };
    


    return ( 
        <ProductsectiondetailBlock>
            <ul className="ProductMenu">
                {
                    tabTit.map( (data) => <Menulist key={data.id} active={ activeTab===data.id } onClick={ () => onSelect(data.id) }><li>{data.headmenu}<ul className="depth2"><li>{data.li1}</li><li>{data.li2}</li><li>{data.li3}</li><li>{data.li4}</li></ul></li></Menulist> )
                }
            </ul>
            <div className="Productcontent">

                { activeTab === 0 && <h2>홈스마트</h2> }
                { activeTab === 1 && <h2>가구</h2> }
                { activeTab === 2 && <h2>침대/매트리스</h2> }
                { activeTab === 3 && <h2>홈데코</h2> }


                {
                    tabTit[activeTab].part.map( (data, index) => <Productitem key={index} photo={data.photo} title={data.title} picture={data.picture} name={data.name} explain={data.explain} price={data.price} />)
                }


                { activeTab === 0 && <h3 className="h3">IKEA의 스마트 홈을 매일 경험해 보세요</h3> }
                { activeTab === 1 && <h3 className="h3">업무를 위한 다용도 가구를 경험해 보세요</h3> }
                { activeTab === 2 && <h3 className="h3">편안한 매트리스로 숙면을 취해요</h3> }
                { activeTab === 3 && <h3 className="h3">꽃을 이용하여 여름의 청량함을 담은 작품 만들기</h3> }



                {
                    tabTit[activeTab].part.map( (data, index) => <Productitembottom key={index} photo={data.photo} title={data.title} picture={data.picture} name={data.name} explain={data.explain} price={data.price} />)
                }
            </div>
                
        </ProductsectiondetailBlock>
    );
};

export default Productsection;