import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

const FooterBlock = styled.div`
    background:#f5f5f5;
    .inner { 
        font-size:14px; font-weight:300;
        .footertop {
            display:flex; padding:60px 0;
            .ikeafamily {
                width:20%; margin-right:15%; 
                h3 { font-size:22px;}
                p {  padding:15px 0;  }
                a { 
                    padding: 10px 20px; font-weight:600; background:#000; 
                    color:#fff; border-radius:50px; display:inline-block;
                }
            }
            .depth1 {
                width:65%;
                display:flex; justify-content: space-between;
                li { 
                    font-weight:600; 
                    .depth2 {
                        padding-top: 20px;
                        li { font-weight:300; line-height:2; } 
                    }
                }
            }
        }
        .footerbotoom {
            border-top:1px solid #ccc; padding: 40px 0;
            font-size:12px;
            p { 
                display:flex; justify-content: space-between; 
                span { 
                    span { padding-left:15px; }
                }
            }
            p:nth-child(1) { padding-bottom:30px; }
        }
    }
`;

const Footer = () => {
    return (
        <FooterBlock>
            <div className="inner">
                <div className="footertop">
                    <div className="ikeafamily">
                        <h3>IKEA Family</h3>
                        <p>지금 IKEA Family에 무료로 가입하고 다양한 멤버 전용 혜택을 누리세요. 자세히 보기</p>
                        <Link to="/family">IKEA Family 가입하기</Link>
                    </div>
                    <ul className="depth1">
                        <li>고객문의
                            <ul className="depth2">
                            <li>고객지원</li>
                            <li>자주 묻는 질문</li>
                            <li>문의하기</li>
                            <li>배송조회</li>
                            <li>교환환불</li>
                            <li>품질보증</li>
                            <li>제품리콜</li>
                            <li>제품 구매 가이드</li>
                            <li>피드백</li>
                        </ul>
                        </li>
                        <li>쇼핑하기
                            <ul className="depth2">
                            <li>쇼핑하기</li>
                            <li>전화 주문</li>
                            <li>IKEA for Business</li>
                            <li>셀프 플래닝</li>
                            <li>IKEA 모바일 앱</li>
                            <li>IKEA 카탈로그</li>
                            <li>제품 사용 팁/가이드</li>
                            <li>결제 옵션</li>
                            <li>기프트 카드</li>
                        </ul>
                        </li>
                        <li>서비스
                            <ul className="depth2">
                            <li>IKEA 서비스</li>
                            <li>배송 서비스</li>
                            <li>조립 서비스</li>
                            <li>주방 서비스</li>
                            <li>욕실 서비스</li>
                            <li>홈퍼니싱 플래닝 서비스</li>
                            <li>인테리어 디자인 서비스</li>
                            <li>바이백 서비스</li>
                        </ul>
                        </li>
                        <li>IKEA 이야기
                            <ul className="depth2">
                            <li>기업 소개</li>
                            <li>데모크래틱 디자인</li>
                            <li>지속가능한 생활</li>
                            <li>뉴스</li>
                            <li>채용정보</li>
                        </ul>
                        </li>
                    </ul>
                </div>
                <div className="footerbotoom">
                    <p><span>© Inter IKEA Systems B.V 1999-2021</span> <span><span>개인정보처리방침</span> <span>웹사이트 이용약관</span> <span>Responsible disclosure</span></span></p>
                    <p>IKEA 코리아</p>
                    <p>주소 : (우) 14352 경기도 광명시 일직로 17 IKEA광명점</p>
                    <p>사업자 등록번호 : 106-86-82871 사업자정보확인</p>
                    <p>대표자 : 프레드릭 요한손</p>
                    <p>통신판매업 신고 : 2018-경기광명-0209</p>
                    <p>TEL : 1670-4532</p>
                
                </div>
            </div>
        </FooterBlock>
    );
};

export default Footer;