import React, { } from 'react';
import styled, {css} from 'styled-components';
import Homeideajson from './homeidea.json';
import HomeideaItem from './HomeideaItem';

const HomeideaBlock = styled.div`
    
    .tabs { display:flex; }
    .content {display:flex; flex-wrap:wrap;} 
`;

const List = styled.div`
    padding:7px 25px; background:#f5f5f5; border-radius:50px;
    cursor:pointer; margin-right:15px;  margin-bottom:20px; font-weight:600;
    border:1px solid #fff;
    ${
        props => props.active && css` border:1px solid #000; `
    }
`;

const tabTit = [
    { id:0, title:"거실", part:Homeideajson.part1 },
    { id:1, title:"침실", part:Homeideajson.part2 },
    { id:2, title:"홈오피스", part:Homeideajson.part3 },
    { id:3, title:"아웃도어", part:Homeideajson.part4 }
]

 

const Homeidea = ({onSelect, activeTab}) => { 
    return (
        <div>
            <HomeideaBlock>
                <div className="tabs"> 
                    {
                        tabTit.map( (data) => <List key={data.id} active={ activeTab===data.id } onClick={ () => onSelect(data.id) }>{data.title}</List> )
                    }
                </div>
                <div className="content">
                    {
                        tabTit[activeTab].part.map( (data, index) => <HomeideaItem key={index} link={data.link} name={data.name} descript={data.descript} price={data.price} photo={data.photo} photo1={data.photo1} photo2={data.photo2} title={data.title} explain={data.explain} />)
                    }
                </div>
            </HomeideaBlock>
        </div>
    );
};

export default Homeidea;