import React from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import Salesection from '../components/Salesection';


const Sale = () => {

    const scrollTo = (ref) => {
        window.scroll({
          top: ref.current.offsetTop,
          behavior: "smooth", 
        });
      };


    return (
        <div>
            <Header />
            <Salesection scrollTo={scrollTo} /> 
            <Footer />  
        </div>
    );
};

export default Sale;