import React, { useEffect }  from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {IoIosArrowForward} from "react-icons/io";

const IdeadetailBlock = styled.div`
    margin-top:100px;
    .inner {
        .idmenu { 
            font-size: 14px; font-weight:300; 
            padding-bottom:15px; border-bottom:1px solid #ddd;
            a { padding-right:10px; &:hover { text-decoration:underline; } }
            span { padding-left:10px; }
        }
        .idcontent { 
            display:flex; padding:60px 0; 
            .idright { 
                width:55%; margin-right:9%; 
                position: relative; 
                .iddotbox {
                    position: absolute; top: 50%; left: 50%; transform: translate(-50%,-50%);
                    .iddot { 
                        background: rgba(0,0,0,0.4); opacity:1;
                        width: 35px; height: 35px; border-radius: 50%;
                        position: relative; transition: all 0.5s;
                        border: 2px solid rgba(255,255,255,0);
                        span { 
                            background: #fff; 
                            width: 14px; height: 14px; border-radius: 50%;
                            box-shadow: 0 0 5px #333;
                            position: absolute; top: 50%; left: 50%; transform: translate(-50%,-50%);
                        }
                        .iddottxt1 { 
                            background: #fff; box-shadow: 0 0 5px #333; 
                            padding: 12px; opacity: 1; transition: all 0.5s;
                            position: absolute; top: 110%; width:207px;
                        }
                    }
                }
            }
            .idleft { 
                width:36%; 
                h2 { font-size: 25px; padding-bottom: 20px}
                p { font-size: 14px; }
                .idcont {
                    display:flex; padding: 80px 0 30px; 
                    border-bottom: 1px solid #ddd;
                    .idimg { width: 25%; }
                    .iddottxt2 {
                        padding-left:30px; line-height:2;
                        p:nth-of-type(2) { font-size: 22px; font-weight:800;}
                    }
                }
            }
        }
    }
`;



const Idea = (props) => {
    console.log(props)
    const {location, history} = props

    useEffect(
        () => {
            if (location.state === undefined) {
                history.push('/')
            } 
        }
        ,[]
    )
    if (location.state) {
        return (
            <div>
                <Header />
                <IdeadetailBlock>
                    <div className="inner">
                        <div className="idmenu">
                            <p>
                                <Link to="/">홈</Link><IoIosArrowForward /><span>홈퍼니싱 아이디어</span>
                            </p>
                        </div>
                        <div className="idcontent">
                            <div className="idright">
                                <img src={location.state.photo} alt={location.state.name} />
                                <div className="iddotbox">
                                    <div className="iddot">
                                        <span></span>
                                        <div className="iddottxt1">
                                            <h3>{location.state.name}</h3>
                                            <p>{location.state.descript}</p>
                                            <p>{location.state.price}</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="idleft">
                                <div className="idtext">
                                    <h2>{location.state.title}</h2>
                                    <p>{location.state.explain}</p>
                                </div>
                                <div className="idcont">
                                    <div className="idimg">
                                        <img src={location.state.photo1} alt={location.state.name} />
                                    </div>
                                    <div className="iddottxt2">
                                        <h3>{location.state.name}</h3>
                                        <p>{location.state.descript}</p>
                                        <p>{location.state.price}</p>
                                    </div>
                                </div>
                            </div>
                        </div>                    
                    </div>
                </IdeadetailBlock>
                <Footer />
            </div>
        );

    } else {
        return null
    }
    
};

export default Idea;