import React from 'react';
import styled from 'styled-components';

import {Link} from 'react-router-dom';

const MemberFormBlock = styled.div`
    display:flex;
    height:100vh;
    .memberh2 { 
        background:#0058a3; width:40%;
        position:relative;
        .h2{
            position:absolute; top:30%; right:50px;
            color:#fff; 
            h2 { color:#fbd914; font-size:36px; span { color:#fff; padding-left:10px;}  }
            p { font-weight:100; a { text-decoration: underline; } }
        }
    }
    .formbox {
        padding:300px 50px; width:60%
    }
`;

const StyledInput = styled.input`
    width:50%; display:block; padding:15px 10px; border:none;
    margin-bottom:20px; border-bottom:1px solid #aaa;
`;
const Button = styled.button`
    width:50%; display:block;
    font-size:16px; font-weight:600;
    padding:15px 10px;
    color:#fff;
    background:#0058a3;
    border-radius:50px;
    border:none;
    margin-top:10px;
    &:hover { background:#333; }
    &.gray {  background:#eee; color:#000; &:hover { background:#ccc; } }
`;


const textType = {
    login:'로그인',
    join:'회원가입'
} 

const MemberForm = ({type}) => {
    const text = textType[type]
    return (
        <MemberFormBlock>
            <div className="memberh2">
                <div className="h2">
                <h2>IKEA Family<span>{text}</span></h2>
                    {
                        type==='join' ? <p>이미 가입하셨나요? <Link to="/login">로그인하기</Link></p> : <p>외워야 할 비밀번호가 많아 불편하셨죠?
                        <br />이제 일회용 코드를 이용하여 간편하게 로그인하세요.</p>
                    }
                </div>
            </div>
            <div className="formbox">
                <form>
                    {
                        type==='join' && <StyledInput type="text" placeholder="이름" name="username" />
                    }
                    <StyledInput type="text" placeholder="이메일 또는 휴대폰 번호" name="userid" />
                    <StyledInput type="password" placeholder="비밀번호" name="password" />
                    {
                        type==='join' && (
                            <>
                            <StyledInput type="password" placeholder="비밀번호확인" name="passok" />
                            <label>성별 : </label> 남 <input type="radio" name="gender" value="M" /> 여 <input type="radio" name="gender" value="F" />
                            </>
                        )
                    }
                    <Button type="submit">{text}</Button>
                    {
                        type==='login' && <Link to="/join"><Button className="gray">회원가입</Button></Link>
                    }
                </form>
            </div>
        </MemberFormBlock>
    );
};

export default MemberForm;