import React, {useState} from 'react';
import { Link } from 'react-router-dom'
import styled from 'styled-components';



const FunitureItemBlock = styled.div`
    float: left; padding-right:15px;
    .furtxt { 
        padding-top: 20px; line-height:2;
        p { font-weight: 300; }
        p:nth-of-type(2) { font-size:22px; font-weight: 800; }
    }
`;

const FunitureItem = ({name, photo, photo2, description, price, score, explain}) => {
    const [change, setChange] = useState( photo );
    const changPhoto = () => {
        setChange(photo2)
    };
    const changPhoto2 = () => {
        setChange(photo)
    };

    return (
        <FunitureItemBlock onMouseEnter={changPhoto} onMouseOut={changPhoto2} >
            <Link to={{pathname:'/furitemdetail', state:{name, photo, photo2, description, price, score, explain}}}>
                <img src={change} alt={name} />
                <div className="furtxt"> 
                    <h3>{name}</h3>
                    <p>{description}</p>
                    <p>{price}</p>
                    <p>{score}</p>
                </div>
            </Link>
        </FunitureItemBlock>
    );
};

export default FunitureItem;